import { useEffect, useState } from 'react';
import { initializeApp } from "firebase/app";
import {
  getAuth
} from "firebase/auth";
import { getDatabase, ref, set, child, get } from "firebase/database";
import { Diagram } from './Diagram';
import Login from './Login';
import Current from './Current';
import Naming from './Naming';
import ResponsiveAppBar, { PageType } from './ResponsiveAppBar';
import Box from '@mui/material/Box';

const firebaseConfig = {
  apiKey: "AIzaSyCwd5DEZmjQD0_AUJLUQcU6wh9u0--i6bQ",
  authDomain: "esp-temp-sens.firebaseapp.com",
  databaseURL: "https://esp-temp-sens-default-rtdb.europe-west1.firebasedatabase.app",
  projectId: "esp-temp-sens",
  storageBucket: "esp-temp-sens.appspot.com",
  messagingSenderId: "962595985757",
  appId: "1:962595985757:web:4434e9adbf1ec39cc8b0b4"
};

const app = initializeApp(firebaseConfig);
const auth = getAuth(app);
const database = getDatabase(app);
const dbRef = ref(database);

function App() {

  const [mapping, setMapping] = useState<{ [key: string]: string }>({});
  const [page, setPage] = useState<PageType>("Aktuális");


  const readData = () => {
    get(child(dbRef, 'mapping')).then((snapshot) => {
      if (snapshot.exists()) {
        setMapping(snapshot.val());
      }
    });
  };

  useEffect(() => { readData() }, []);

  return (
    <>
      <ResponsiveAppBar setSelectedPage={setPage} />
      <Box sx={{ paddingTop: "8px" }} >
        {!auth.currentUser && <Login auth={auth} />}
        {auth.currentUser && page === "Aktuális" && <Current database={database} />}
        {auth.currentUser && page === "Elnevezések" && <Naming database={database} />}
        {auth.currentUser && page === "Diagram" && <Diagram dbRef={dbRef} mapping={mapping} />}
      </Box>
    </>
  );
}

export default App;
