import React, { useEffect, useState } from 'react';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import {
    Auth,
    signInWithEmailAndPassword,
} from "firebase/auth";

function Login(props: {auth: Auth}) {

    const [email, setEmail] = useState<string>();
    const [password, setPassword] = useState<string>();

    const logInWithEmailAndPassword = () => {
        email && password && signInWithEmailAndPassword(props.auth, email, password).then(() => {
            setEmail("");
            setPassword("");
        });
    };

    return (
        <Grid container spacing={2} direction="column" alignItems="center" justifyContent="center">
            <Grid item >
                <TextField label="email" variant="outlined" onChange={(e) => setEmail(e.target.value)} />
            </Grid>
            <Grid item >
                <TextField label="password" variant="outlined" onChange={(e) => setPassword(e.target.value)} />
            </Grid>
            <Grid item xs={12}>
                <Button onClick={logInWithEmailAndPassword} variant="outlined" disabled={!email || !password}>Login</Button>
            </Grid>
        </Grid>
    );
}

export default Login;
