import React, { useEffect, useState, useRef } from 'react';
import { DatabaseReference, limitToLast } from "firebase/database";
import { Chart, GoogleChartWrapperChartType, ChartWrapperOptions } from "react-google-charts";
import { child, get, query, orderByChild, limitToFirst, startAt } from "firebase/database";
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import { Typography } from '@mui/material';

const START_AT_TIME_BEFORE = 48 * 60 * 60 * 1000; /* ms */
const STARTING_TIME_RANGE = 2 * 60 * 60 * 1000; /* ms */

export const Diagram: React.FC<{ dbRef: DatabaseReference; mapping: { [key: string]: string } }> = ({ dbRef, mapping }) => {
    const ref = useRef(null);
    const [historicData, setHistoricData] = useState<{ [key: string]: { [key: string]: number } }>({});
    const [data, setData] = useState<(string | { type: string; label: string; } | Date | number)[][]>([]);
    const [chartType, setChartType] = useState<GoogleChartWrapperChartType>("LineChart");


    useEffect(() => {
        function touchHandler(event: any) {
            var touches = event.changedTouches,
                first = touches[0],
                type = "";
            switch (event.type) {
                case "touchstart": type = "mousedown"; break;
                case "touchmove": type = "mousemove"; break;
                case "touchend": type = "mouseup"; break;
                default: return;
            }

            // initMouseEvent(type, canBubble, cancelable, view, clickCount, 
            //                screenX, screenY, clientX, clientY, ctrlKey, 
            //                altKey, shiftKey, metaKey, button, relatedTarget);

            var simulatedEvent = document.createEvent("MouseEvent");
            simulatedEvent.initMouseEvent(type, true, true, window, 1,
                first.screenX, first.screenY,
                first.clientX, first.clientY, false,
                false, false, false, 0/*left*/, null);

            first.target.dispatchEvent(simulatedEvent);
        }

        window.addEventListener('touchstart', touchHandler);
        window.addEventListener('touchmove', touchHandler);
        window.addEventListener('touchend', touchHandler);
        window.addEventListener('touchcancel', touchHandler);

        return () => {
            window.removeEventListener('touchstart', touchHandler);
            window.removeEventListener('touchmove', touchHandler);
            window.removeEventListener('touchend', touchHandler);
            window.removeEventListener('touchcancel', touchHandler);
        };
    }, []);

    useEffect(() => {
        const tempData: (string | { type: string; label: string; role?: string } | Date | number)[][] = [];
        Object.entries(mapping).map(([key, value]) => {

        });
        tempData.push([
            { label: "Day", type: "date", role: "domain" },
            ...Object.values(mapping).flatMap((value) => { return [{ label: value, type: "number" }, { label: "tooltip", type: 'string', role: 'tooltip' }] }),
        ]);
        Object.values(historicData).map((entry) => {
            const row: (Date | number | string)[] = [new Date(entry['timestamp'])];
            const tooltip = new Date(entry['timestamp']).toISOString() + "\n" + Object.entries(mapping).map(([key, value]) => {
                return value + ": " + (entry[key] ?? "x");
            }).join("\n");
            Object.entries(mapping).map(([key, value]) => {
                row.push(entry[key]);
                row.push(tooltip)
            });
            tempData.push(row);
        });
        setData(tempData);
    }, [historicData, mapping])

    const readData = () => {
        const recentHistoricRef = query(child(dbRef, 'historic'), orderByChild('timestamp'), startAt(Date.now() - START_AT_TIME_BEFORE))
        get(recentHistoricRef).then((snapshot) => {
            if (snapshot.exists()) {
                setHistoricData(snapshot.val());
            }
        });
    };

    const dateMinus = new Date(Date.now() - STARTING_TIME_RANGE);
    const actualDate = new Date(Date.now());

    const options: ChartWrapperOptions["options"] = {
        legend: { position: "top" },
        hAxis: {
            format: 'd. HH:mm',
        }
    };

    return (
        <>
            <Grid container spacing={2} alignItems="center" justifyContent="center">
                <Grid item>
                    <Button disabled={chartType === "LineChart"} onClick={() => setChartType("LineChart")} variant="outlined" color='secondary'>Vonal</Button>
                </Grid>
                <Grid item>
                    <Button disabled={chartType === "ScatterChart"} onClick={() => setChartType("ScatterChart")} variant="outlined" color='secondary'>Pont</Button>
                </Grid>
            </Grid>
            {data.length > 1 && (
                <Chart
                    ref={ref}
                    chartType={chartType}
                    data={data}
                    width="100vw"
                    height="100vh"
                    options={options}
                    chartPackages={["corechart", "controls", "timeline"]}
                    controls={[
                        {
                            controlType: "ChartRangeFilter",
                            options: {
                                filterColumnIndex: 0,
                                ui: {
                                    chartType: "LineChart",
                                    chartOptions: {
                                        hAxis: { baselineColor: "none", format: 'd. HH:mm' }
                                    }
                                }
                            },
                            controlPosition: "bottom",
                            controlWrapperParams: {
                                state: {
                                    range: {
                                        start: dateMinus,
                                        end: actualDate
                                    }
                                }
                            }
                        }
                    ]}
                />)}
        </>
    )

}
